body {
  margin: 0;
  padding: 20px;
  background-color: rgb(221, 246, 246);
  display: flex;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.app {
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.05) 0 6px 24px, rgba(0, 0, 0, 0.08) 0 0 0 1px;
  border-radius: 10px;
  padding: 10px;
  width: 800px;
  margin-top: 10px;
}

.button-container,
.info-container {
  display: flex;
  align-items: center;
  padding: 10px;
}

.button-container button {
  margin: 0 5px;
  padding: 5px 10px;
  border-radius: 12px;
  background-color: transparent;
  font-size: 10px;
}

.edit {
  border: 1.5px solid rgb(134, 183, 183);
  color: rgb(134, 183, 183);
  background-color: transparent;
  text-transform: uppercase;
}

.edit:hover {
  background-color: rgb(182, 223, 186);
}

.edit:active {
  border: 1.5px solid rgb(134, 183, 183);
  color: rgb(255, 255, 255);
  background-color: rgb(141, 181, 145);
}

.delete {
  border: 1.5px solid rgb(255, 175, 163);
  color: rgb(255, 175, 163);

  text-transform: uppercase;
}

.delete:hover {
  background-color: rgb(255, 201, 193);
}

.delete:active {
  border: 1.5px solid rgb(255, 175, 163);
  color: rgb(255, 255, 255);
}

.create {
  border: 1.5px solid rgb(108, 115, 148);
  color: rgb(108, 115, 148);
  text-transform: uppercase;
}

.create:hover {
  background-color: rgb(108, 115, 148);
  color: white;
}

.create:active {
  background-color: rgb(108, 115, 148);
  color: white;
}

.signout {
  border: 1.5px solid rgb(255, 175, 163);
  color: rgb(234, 234, 234);
  background-color: rgb(255, 175, 163) !important;
}

.signout:hover {
  background-color: rgb(175, 94, 94) !important ;
  border: 1.5px solid rgb(175, 94, 94);
}

.signout:active {
  background-color: rgb(175, 81, 81) !important;
  border: 1.5px solid rgb(175, 81, 81) !important;
  color: white;
}

