.item {
  display: flex;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  width: 100%;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.08) 0 0 0 1px;
  margin: 10px 0;
  justify-content: space-between;
}

.check-circle {
  width: 24px;
  height: 24px;
  margin-left: 12px;
  border-radius: 16px;
  border: 1px solid #ced4da;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  cursor: pointer;
}

.checked {
  border: 1px solid #38d9a9;
  color: #38d9a9;
}

.text {
  flex: 1;
  font-size: 15px;
  color: #495057;
}

.done-text {
  color: #ced4da;
}

.button-container {
  display: flex;
  align-items: center;
  padding: 10px;
}

.button-container button {
  margin: 0 5px;
  padding: 5px 10px;
  border-radius: 12px;
  background-color: transparent;
  font-size: 10px;
}

.edit {
  border: 1.5px solid rgb(134, 183, 183);
  color: rgb(134, 183, 183);
  background-color: transparent;
  text-transform: uppercase;
}

.edit:hover {
  background-color: rgb(182, 223, 186);
}

.edit:active {
  border: 1.5px solid rgb(134, 183, 183);
  color: rgb(255, 255, 255);
  background-color: rgb(141, 181, 145);
}

.remove {
  border: 1.5px solid rgb(255, 175, 163);
  color: rgb(255, 175, 163);

  text-transform: uppercase;
}

.remove:hover {
  background-color: rgb(255, 201, 193);
}

.remove:active {
  border: 1.5px solid rgb(255, 175, 163);
  color: rgb(255, 255, 255);
}

.input {
  margin-left: 20px;
  padding: 12px 16px;
  border-radius: 12px;
  border: 1.5px solid rgb(230, 232, 236);
  width: 70%;
}
