.circle-button {
  background: #38d9a9;
  z-index: 5;
  cursor: pointer;
  width: 80px;
  height: 80px;
  display: block;
  align-items: center;
  justify-content: center;
  font-size: 60px;
  position: absolute;
  left: 50%;
  bottom: 0px;
  transform: translate(-50%, 50%);
  color: white;
  border-radius: 50%;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;

  transition: 0.125s all ease-in;
}

.circle-button:hover {
  background: #63e6be;
}

.circle-button:active {
  background: #20c997;
}

.opened {
  background: #ff6b6b;
  transform: translate(-50%, 50%) rotate(45deg);
}

.opened:hover {
  background: #ff8787;
}

.opened:active {
  background: #fa5252;
}

.insert-form-positioner {
  width: 100%;
  bottom: 0;
  left: 0;
  position: absolute;
}

.insert-form {
  background: #f8f9fa;
  padding-left: 32px;
  padding-top: 32px;
  padding-right: 32px;
  padding-bottom: 72px;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  border-top: 1px solid #e9ecef;
  display: flex;
  align-items: center;
}

