.input {
  margin: 11px 0;
  padding: 12px 16px;
  border-radius: 12px;
  border: 1.5px solid rgb(230, 232, 236);
  width: 70%;
}

.button-container {
  display: flex;
  align-items: center;
  padding: 10px;
}

.button-container button {
  margin: 0 5px;
  padding: 5px 10px;
  border-radius: 12px;
  background-color: transparent;
  font-size: 10px;
}

.create {
  border: 1.5px solid rgb(108, 115, 148);
  color: rgb(108, 115, 148);
  text-transform: uppercase;
}

.create:hover {
  background-color: rgb(108, 115, 148);
  color: white;
}

.create:active {
  background-color: rgb(108, 115, 148);
  color: white;
}
